@import "src/style/global-import.scss";






































@include theme {
    .pl-notice-container {
        position: fixed;
        z-index: 9999;
        padding: 20px;

        .pl-notice-service {
            margin-bottom: 16px;

            .pl-notice-service-content {
                background-color: white;
                width: 300px;
                box-shadow: 0 2px 12px 0 rgba(0, 0, 0, .1);
                padding: 12px 16px;
                position: relative;
                border-radius: $shapeFillet;
                border: solid 1px $ibc;
            }

            .pl-notice-service-head {
                font-size: 16px;
                color: $ihc;
            }

            .pl-notice-service-body {
                font-size: 14px;
                padding-top: 16px;
                color: $itc;
                max-height: 300px;
                overflow-y: auto;
            }

            .pl-notice-service-close {
                position: absolute;
                top: 8px;
                right: 8px;

                .pl-icon {
                    color: $icc
                }

                &:hover {
                    .pl-icon {
                        color: inherit;
                    }
                }
            }

            .pl-notice-service-status-icon {
                position: absolute;
                top: 12px;
                left: 16px;
            }

            @include statusMixin(notice-service) {
                .pl-notice-service-content {
                    padding-left: 56px;
                }
                .pl-notice-service-status-icon {
                    color: $value;
                    font-size: 24px;
                }
            }
        }

        &.pl-notice-container-start-start {
            top: 0;
            left: 0;
        }

        &.pl-notice-container-start-end {
            top: 0;
            right: 0;
        }

        &.pl-notice-container-end-start {
            bottom: 0;
            left: 0;
        }

        &.pl-notice-container-end-end {
            bottom: 0;
            right: 0;
        }
    }
}
