@import "src/style/global-import.scss";

























































@include theme {

    .pl-loading {
        @include statusMixin(loading) {
            color: $value;
        }
    }
    .pl-loading-mask {
        color: $colorPrimary;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(255, 255, 255, 0.5);
        transition: all linear 300ms;
        transform-origin: center center;
        cursor: progress;
        pointer-events: auto;

        .pl-loading {
            margin-bottom: 20px;
            font-size: 32px;

            & + span {
                font-size: 14px;
            }
        }

        &.pl-loading-mask-unlock {
            pointer-events: none;
        }

        &.pl-loading-mask-fixed-position {
            position: fixed;
        }
    }
}

.pl-transition-loading-mask-enter-active, .pl-transition-loading-mask-leave-active {
    opacity: 1;
}

.pl-transition-loading-mask-enter, .pl-transition-loading-mask-leave-to {
    opacity: 0;
}
