@import "src/style/global-import.scss";
@include theme {
  .pl-color-alpha-slider {
    width: 10px;
    background-size: 10px 10px;
    position: relative;
    cursor: pointer;
    border-radius: 10px;
    display: inline-block;

    .pl-color-alpha-shadow, .pl-color-alpha-thumb {
      pointer-events: none;
    }

    .pl-color-alpha-shadow {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      border-radius: 10px;
    }

    .pl-color-alpha-thumb {
      width: 10px;
      height: 10px;
      border-radius: 10px;
      border: solid 1px $colorInfo;
      display: inline-block;
      background-color: white;
      position: absolute;
      top: 0;
      left: 0;
      box-sizing: border-box;
    }
  }

  .pl-color-hue-slider {
    background: linear-gradient(90deg, red 0, #ff0 17%, #0f0 33%, #0ff 50%, #00f 67%, #f0f 83%, red);
    display: inline-block;
    height: 10px;
    position: relative;
    cursor: pointer;
    border-radius: 10px;

    .pl-color-hue-slider-thumb {
      display: inline-block;
      height: 100%;
      background-color: white;
      position: absolute;
      top: 0;
      border: solid 1px $colorInfo;
      box-sizing: border-box;
      border-radius: 100px;
    }
  }

  .pl-color-sv-panel {
    position: relative;
    box-shadow: 0 0px 4px 0 rgba(0, 0, 0, 0.4);
    cursor: pointer;
    display: inline-block;

    &:before, &:after, & span {
      pointer-events: none;
    }

    &, &:before, &:after {
      border-radius: 2px;
    }

    &:before, &:after {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      content: '';
    }

    &:before {
      background: linear-gradient(to right, white, rgba(255, 255, 255, 0));
    }

    &:after {
      background: linear-gradient(to top, black, rgba(255, 255, 255, 0));
    }

    .pl-color-sv-thumb {
      display: inline-block;
      position: absolute;
      width: 1px;
      height: 1px;
      left: 0;
      top: 0;
      z-index: 1;

      &:after {
        display: inline-block;
        width: 10px;
        height: 10px;
        border: solid 1px #999;
        border-radius: 10px;
        position: absolute;
        top: -5px;
        left: -5px;
        content: '';
        box-sizing: border-box;
        background-color: white;
      }
    }
  }

  .pl-color-panel {
    display: inline-block;
    font-size: 0;

    .pl-color-hue-slider {
      margin-top: 8px;
      display: block;
    }

    .pl-color-alpha-slider {
      margin-left: 8px;
    }

    .pl-color-panel-input-group {
      margin-top: 8px;

      .pl-input {
        margin-right: 8px;

        .pl-input-inner {
          text-align: center;
        }
      }
    }
  }

  .pl-color-picker {
    .pl-color-picker-suffix {
      border-radius: 2px;

      .pl-color-picker-suffix-inner {
        /*background-color: rgba(0, 0, 0, 0.5);*/
        border-radius: 2px;
        height: 100%;
        width: 100%;

      }
    }

    @include sizeMixin(input) {
      .pl-color-picker-suffix {
        height: $value/2;
        width: $value/2;
        background-size: $value/4, $value/4;
      }
    }

  }

}